import React from 'react';
import ReactDom from 'react-dom';
import Dropdown from '../index';

class DropdownAsnycDemo extends React.Component {

  constructor(props) {
    super(props);
    this.loadOptions = this.loadOptions.bind(this);
    this.state = {
      options: null
    };
  }

  render() {
    let { options } = this.state;
    return (
      <div onClick={this.loadOptions}>
        <Dropdown
          options={options}
          style={{ width: '200px' }}
        />
      </div>
    );
  }

  loadOptions() {
    setTimeout(() => {
      this.setState({
        options: [
          {
            name: 'swift',
            value: 101
          }, {
            name: 'java',
            value: 121
          }, {
            name: 'javascript',
            value: 332
          }
        ]
      });
    }, 2000);
  }
}

ReactDom.render(<DropdownAsnycDemo />, document.getElementById('asnyc'));
